import React, { useState } from 'react';
import { AppstoreOutlined, BulbOutlined, BuildOutlined, SmileOutlined } from '@ant-design/icons';
import { Menu } from 'antd';

function getItem(label, key, icon, children, type) {
  return {
    key,
    icon,
    children,
    label,
    type,
  };
}

const CatalogMenuItems = [
  getItem(' ', 'balloons', <BulbOutlined />, [
    getItem('14 ', '1'),
    getItem(' ', '2'),
    getItem('-', '3'),
    getItem(', ', '4'),
    getItem('  ', '5'),
    getItem('    ', '6'),
    getItem(', ', '7'),
    getItem(' ', '8'),
    getItem(', , ', '9'),
  ]),
  getItem('', 'photozone', <AppstoreOutlined />, [
    getItem(' ', '10'),
    getItem(' ', '11'),
    getItem(' ', '12'),
  ]),
  getItem('  ', 'animation', <SmileOutlined />, [
    getItem('-', '13'), 
    getItem('  ', '14'),
    getItem(' ', '15'),
  ]),
  getItem('  ', 'attractions', <BuildOutlined />, [
    getItem('', '16', null, 
      [getItem(' ', '17'), 
      getItem(' ', '18'),
      getItem(' ', '19'),
    ]),
    getItem('  ', '20', null, 
      [getItem(' ', '21'), 
      getItem('', '22'),
      getItem('  ', '23'),
    ]),
  ]),
];

const onClick = (e) => {
  console.log(e.keyPath);
};

// submenu keys of first level
const rootSubmenuKeys = ['balloons', 'photozone', 'animation', 'attractions'];

const CatalogMenu = ({style}) => {
  const [openKeys, setOpenKeys] = useState(['']);

  const onOpenChange = (keys) => {
    const latestOpenKey = keys.find((key) => openKeys.indexOf(key) === -1);
    if (latestOpenKey && rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
      setOpenKeys(keys);
    } else {
      setOpenKeys(latestOpenKey ? [latestOpenKey] : []);
    }
  };
  
  return (
    <Menu
      onClick={onClick}
      style={style}
      openKeys={openKeys}
      onOpenChange={onOpenChange}
      mode="inline"
      items={CatalogMenuItems}
    />
  )
};

export default CatalogMenu;
