import React from 'react';
import { Input, ConfigProvider } from 'antd';

const { Search } = Input;

const onSearch = (value, _e, info) => console.log(info?.source, value);

const SearchComponent = ({className}) => (
  <ConfigProvider
    theme={{
      token: {
        colorPrimary: '#f83939'
      },
    }}
  >
    <Search
      className={className}
      placeholder=" "
      onSearch={onSearch}
      maxLength={50}
      size={'large'}
      enterButton
    />
  </ConfigProvider>
  
);
export default SearchComponent;
